<template>
    <div id="create_lobby" class="lobby">
        <p id="title">   </p>
        <div id="create_info">

            <p class="help">    </p>
            <input class="input_box" type="text" v-model="room_name" placeholder="  "/>
            <div class="margin_area"></div>
            <p class="help">    </p>
            <input class="input_box" type="text" v-model="room_pw" placeholder=" "/>
            <div class="margin_area"></div>
            <p class="help">   </p>
            <p class="sm_help">        . </p>
            <select v-model="selected" id="room_max_count_select">
                <option v-for="board in list" :value="board.val" :key="board">
                        {{board.selected}}</option>
            </select>

            <div class="margin_area"></div>
            <button class="create_game" v-on:click="create_game" >   </button>
            <br>
            <button class="back_main" v-on:click="back_first_page">  </button>
            <div class="end_area"></div>
        </div>
    </div>
</template>

<script>

export default {
    el : '#create_lobby',
    name: 'create_lobby',
     props: {
        //   room_list
        room_list : {
            type : Array,
            required : true
        },
        name : {
            type : String,
        },
        re_page : {
            type : Boolean,
        }
    },
    created() {
        //console.log(this.$route.params);
    },
    data() {
        return {
            room_name : '',
            room_pw : '',
            count : '',
            selected : '2',
            list : [
                {selected: '2', val: 2},
                {selected: '3', val: 3},
                {selected: '4', val: 4}
            ]
        }
    },
    methods: {
        create_game : function() {
            //    .
            for( let i = 0; i < this.room_list.length; i++ ){
                if( this.room_list[i].name == this.room_name ){
                    //   
                    alert("    .\n  !");
                    return;
                }
            }

            this.$router.push({
                name: 'game_lobby',
                params: {
                    room_name: this.room_name,
                    room_pw: this.room_pw,
                    count: this.selected,
                    name: this.name,
                    is_master : true,
                    restart_counter : this.re_page,
                }
            });
        },
        back_first_page : function() {
            this.$emit("back_page");
        },
        change_count : function(cnt) {
            console.log(cnt);
            this.selected = cnt;
        }
    },

}

</script>

<style scoped src="@/assets/css/create_lobby.css"></style>